// pages / dashboard / conversation / message

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link from "gatsby-link";

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
// import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
// import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class Settings extends React.Component {

    render() {
        if (this.props.user.loggedIn) {

            return (
                <span>

                    {/* Start of Helmet */}
                    <Helmet title="Settings" meta={[
                        {
                            name: 'description',
                            content: 'Settings'
                        }, {
                            name: 'keywords',
                            content: 'Settings'
                        }
                    ]}/>
                    {/* End of Helmet */}


                    {/* Start of Heading */}
                    <div className="row wrapper border-bottom white-bg page-heading">
                        <div className="col-lg-10">
                            <h2>Settings</h2>
                            <ol className="breadcrumb">
                                <li>
                                    <Link to="/dashboard">Home</Link>
                                </li>
                                <li className="active">
                                    <strong>Personal Settings</strong>
                                </li>
                            </ol>
                        </div>
                        <div className="col-lg-2">
                        </div>
                    </div>
                    {/* End of Heading */}


                    {/* Start of Content */}
                    <div className="wrapper wrapper-content animated fadeInRight">
                        <div className="row">
                            <div className="col-lg-10 col-hcenter">
                                <div className="ibox float-e-margins">
                                    <div className="ibox-title">
                                        <h5>Personal Settings</h5>
                                        <div className="ibox-tools">
                                            <a className="collapse-link">
                                                <i className="fa fa-chevron-up"></i>
                                            </a>
                                            <a className="dropdown-toggle" data-toggle="dropdown" href="#">
                                                <i className="fa fa-wrench"></i>
                                            </a>
                                            <ul className="dropdown-menu dropdown-user">
                                                <li><a href="#">Config option 1</a>
                                                </li>
                                                <li><a href="#">Config option 2</a>
                                                </li>
                                            </ul>
                                            <a className="close-link">
                                                <i className="fa fa-times"></i>
                                            </a>
                                        </div>
                                    </div>
                                    <div className="ibox-content">
                                        <form className="form-horizontal">
                                            <h2>Personal Details</h2>
                                            <div className="space-25"></div>
                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="firstName">First Name</label>
                                                <div className="col-lg-10">
                                                    <input type="text" placeholder="First Name" id="firstName" className="form-control" defaultValue={this.props.user.first_name} required />
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="lastName">Last Name</label>
                                                <div className="col-lg-10">
                                                    <input type="text" placeholder="Last Name" id="lastName" className="form-control" defaultValue={this.props.user.last_name} required />
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="email">Email</label>
                                                <div className="col-lg-10">
                                                    <input type="email" placeholder="Email" id="email" className="form-control" defaultValue={this.props.user.email} required />
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="address">Address</label>
                                                <div className="col-lg-10">
                                                    <input type="text" placeholder="Address" id="address" value="Unit A-13" className="form-control" disabled required />
                                                </div>
                                            </div>


                                            <div className="hr-line-dashed"></div>
                                            <div className="space-30"></div>

                                            <h2>Change Password</h2>
                                            <div className="space-25"></div>
                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="oldPassword">Old Password</label>
                                                <div className="col-lg-10">
                                                    <input type="password" placeholder="Old Password" id="oldPassword" className="form-control" />
                                                </div>
                                            </div>

                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="newPassword">Password</label>
                                                <div className="col-lg-10">
                                                    <input type="password" placeholder="Password" id="newPassword" className="form-control" />
                                                </div>
                                            </div>

                                            <div className="form-group">
                                                <label className="col-lg-2 control-label" htmlFor="reenterPassword">Re-enter Password</label>
                                                <div className="col-lg-10">
                                                    <input type="password" placeholder="Re-enter Password" id="reenterPassword" className="form-control" />
                                                </div>
                                            </div>

                                            <div className="hr-line-dashed"></div>
                                            <div className="form-group">
                                                <div className="col-sm-4 col-sm-offset-2 pull-right">
                                                    <button className="btn btn-primary" type="submit">Save changes</button>
                                                </div>
                                            </div>

                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* End of Content */}


                </span>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user, messages: state.messages.messages, currentMessage: state.messages.currentMessage};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(Settings);



